import { mapState } from 'vuex';
export default {
	data() {
		return {
			// 悬浮按钮
			left: 0,
			right: 10,
			bottom: 60,
			bgColor: '#5677fc',
			btnList: [],
			// 分页数据
			page: 1,
			limit: 10,
			productList: [],
			loadding: false,
			pullUpOn: true,
			operationModal: false,
			buttonList: [
				{
					text: '修改',
					type: 'green',
					plain: true
				},
				{
					text: '删除',
					type: 'red',
					plain: true
				},
				{
					text: '取消',
					plain: true
				}
			],
			id: undefined,
			noLoginModal: false,
			loginButton: [
				{
					text: '确定',
					type: 'red'
				}
			]
		};
	},
	computed: mapState(['isLogin']),
	onLoad() {
		this.page = 1;
		this.productList = [];
		this.getData();
	},
	methods: {
		//页面相关事件处理函数--监听用户下拉动作
		onPullDownRefresh: function() {
			//延时为了看效果
			setTimeout(() => {
				this.pullUpOn = true;
				this.loadding = false;
				this.page = 1;
				this.productList = [];
				this.getData();
				uni.stopPullDownRefresh();
				this.tui.toast('刷新成功');
			}, 200);
		},
		// 页面上拉触底事件的处理函数
		onReachBottom: function() {
			if (!this.pullUpOn) return;
			this.loadding = true;
			this.getData();
		},
		getData: function() {
			const db = wx.cloud.database({
				env: 'jnmc-ronxp'
			});
			db.collection('product')
				.orderBy('create_time', 'desc')
				.skip((this.page - 1) * this.limit)
				.limit(this.limit)
				.get()
				.then(response => {
					this.productList = response.data;
					if (response.data.length < this.limit) {
						this.loadding = false;
						this.pullUpOn = false;
					} else {
						this.pullUpOn = true;
						this.page = this.page + 1;
					}
				});
		},
		gotoDetail: function(id) {
			uni.navigateTo({
				url: '/pages/product/detail?type=detail&id=' + id
			});
		},
		gotoAddProduct: function() {
			// 登录校验
			if (!this.isLogin) {
				this.showNoLoginModal();
			} else {
				uni.navigateTo({
					url: '/pages/product/detail?type=add'
				});
			}
		},
		showOperation: function(id) {
			this.operationModal = true;
			this.id = id;
		},
		hideOperation: function() {
			this.operationModal = false;
		},
		handleOperation: function(e) {
			this.hideOperation();
			// 登录校验
			if (!this.isLogin) {
				this.showNoLoginModal();
			} else {
				let index = e.index;
				switch (index) {
					case 0:
						uni.navigateTo({
							url: '/pages/product/detail?type=edit&id=' + this.id
						});
						break;
					case 1:
						const db = wx.cloud.database({
							env: 'jnmc-ronxp'
						});
						// 删除产品图片
						db.collection('product')
							.doc(this.id)
							.field({
								files: true
							})
							.get()
							.then(response => {
								console.log(response.data);
								const fileIDS = response.data.files.map(item => item.id);
								if (fileIDS && fileIDS.length > 0) {
									wx.cloud.deleteFile({
										fileList: fileIDS,
										success: res => {
											console.log('删除产品图片结果：' + res);
										},
										fail: err => {
											console.log('删除产品图片结果：' + err);
										}
									});
								}
								// 删除产品
								db.collection('product')
									.doc(this.id)
									.remove()
									.then(response => {
										if (response.stats.removed > 0) {
											this.tui.toast('删除产品成功');

											this.page = 1;
											this.productList = [];
											this.getData();
										} else {
											this.tui.toast('删除产品失败');
										}
									})
									.catch(res => {
										console.log('删除产品失败:' + res);
									});
							})
							.catch(res => {
								console.log(res);
							});

						break;
					default:
						break;
				}
			}
		},
		hideNoLoginModal: function() {
			this.noLoginModal = false;
		},
		showNoLoginModal: function() {
			this.noLoginModal = true;
		},
		gotoLogin: function() {
			uni.switchTab({
				url: '/pages/my/index'
			});
			this.hideNoLoginModal();
		}
	}
};
